exports.addNewSession = (params) => {
    return new Promise((resolve, reject)=>{
        const request = new XMLHttpRequest();
        let url = generateSessionQueryURL({
            "phone" : params.phone,
            "uid" : params.uid,
            "location" : params.location,
            "duration" : params.duration
        });
        
        request.open('POST', `/sessionsWorker?${url}`, true);
        try { 
            request.send();
        } catch (err) { 
            console.log(err); 
        }

        request.onreadystatechange = event => {
            let response = request.response;
            console.log(request.readyState);
            console.log(response);
            //console.log(request.status);
            //if (response.state==="SUCCESS" /*request.readyState === 4*/ && request.status === 200) {
                //console.log("SUCCESS");
                resolve({
                    "sid" : response.sid,
                    "startDate" : response.startDate
                });
            //} else {
            //    reject(response);
            //}
        };
    });    
}

function generateSessionQueryURL(query) {
    return (
        `phone=${query.phone}&` +
        `uid=${query.uid}&` +
        `location=${query.locatione}&` +
        `duration=${query.duration}&` +
        `verify=true`
    );
}


